import type React from 'react';
import type { ISelectProps } from '../../../../form/select/Select';
import type { ICommonComponentProps } from '../interface';


export interface IOption {
  key: string;
  icon: React.ReactElement;
  title: string;
  disabled?: boolean;
}
export interface ITextSelectProps extends Omit<ICommonComponentProps, 'title'>,
Pick<ISelectProps, 'options'| 'mode' | 'renderOption' | 'renderValue'> {
  value?: string;

  onChange?: (v: string) => void;

  /**
   * 弹层可见性
   */
  overlayVisible?: boolean;
  /**
    * 弹层可见性变化处理函数
    */
  onVisibleChange?: (e: boolean) => void;
  /**
   * 
   */
  width: number;
  /**
   * 
   */
  keyboard?: boolean;
}

export interface IBaseSelectProps extends ITextSelectProps {
  size: 'normal' | 'small';
}
